.navbar{
    display:flex;
    position:fixed;
    align-items: center;
    justify-content:flex-end;
    justify-items: center;
    background-color:#5b88a5;
    left: 0;
    top: 0;
    width: 100%;
    height: 60px;
    color: #f4f4f2;
    gap: 0;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.linkss{
    display: flex;
    position:relative;
    align-items:flex-end;
    justify-content: flex-end;
    align-content:flex-end;
    margin: 30px;
    gap: 30px;
    justify-items:flex-end;
}

.nosub{
    display: flex;
    position:relative;
    align-items:flex-end;
    justify-content: flex-end;
    align-content:center;
    justify-items:flex-end;
    text-decoration: none;
    color: #f4f4f2;
    font-size: 19px;
}

.nosub:hover{
    color: #243a69;
    transition:all .5s;
}
.nosub:not(:hover){
    color: #f4f4f2;
    transition:all .5s;
}
