@import url('https://fonts.googleapis.com/css2?family=Jaro:opsz@6..72&family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Nabla&family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Roboto+Mono:wght@700&family=Roboto:ital,wght@0,300;1,100;1,300&display=swap');


.contacto{
    display: flex;
    position:absolute;
    width: 100% ;
    height: 100%;
    top: 0;
    left: 0;
    justify-content: space-evenly;
    align-items: center;
    align-content: flex-end;
    list-style: none;
    background: linear-gradient(rgba(255, 255, 255, 0.75), rgba(255, 255, 255, 0.75)), url(../img/code2.jpg) no-repeat center center;
    background-size: cover;
    margin-top: calc(200vh - 60px );
    flex-direction: column;
    box-sizing: border-box;
}

form{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    gap: 0.5rem;
    justify-content: center;
    justify-items: center;
}

form h1{
    font-size: 3rem;
    font-family: "Roboto Mono", monospace;
    font-optical-sizing: auto;
    font-style: normal;
    color: #243a69;
    text-shadow: 6px 6px 6px #243a69;
}

form label{
    display: flex;
    justify-content: flex-start;
    color: #f4f4f2;
    text-shadow: 2px 2px 2px #243a69,
    2px 2px 2px #243a69,
    2px 2px 2px #243a69;
}

form input{
    display: flex;
    width: 20rem;
    height: 2rem;
    background-color: #5b87a591;
    box-shadow: 5px 5px 5px #5b88a5,
    5px 5px 5px #5b88a5,
    5px 5px 5px #5b88a5;
    padding: 5px;
    border:3px solid #243a69;
}

.mensaje{
    height: 6rem;
    display: flex;
    width: 20rem;
    background-color: #5b87a591;
    box-shadow: 5px 5px 5px #5b88a5,
    5px 5px 5px #5b88a5,
    5px 5px 5px #5b88a5;
    padding: 5px;
    border:3px solid #243a69;
}

@media screen and (max-width:1000px) {
    .contacto{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        margin-top: calc(200vh - 60px );
    }
    }