.App {
  display: flex;
  text-align: center;
  flex-direction: column;
  height: 100%;
  flex-wrap: wrap;
  grid-template-areas: 
  "navbar"
  "sobremi"
  "proy"
  "contacto";
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@media (max-width: 768px) {
  .App {
    margin: 0;
    padding: 0;
  }
}